.container{
    height: 100%;
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(to right , #9900ff , #cc80ff);
    display: flex;
    justify-content: center;
    align-items: center;
}

.innerBox{
    min-width: 400px;
    height: fit-content;
    width: fit-content;
    background-color: #fff;
    box-shadow: 1px 1px 4px rgba(0,0,0,0.2);
    padding: 30px;
    border-radius: 10px;
    display: flex;
    text-align: left;
    flex-direction: column;
    gap: 30px;
}

.footer{
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.err{
    text-align: center;
   font-weight: bold;
   font-size: 0.875rem;
   color: #ff3300;
}
.footer button{
    outline: none;
    border: none;
    background-color: #9900ff;
    color: #fff;
    font-weight: bold;
    font-size: 1rem;
    padding: 10px 16px;
    width: 100%;
    cursor: pointer;
    transition: 100%;
}

.footer button:hover{
    background-color: #aa2aff;
}
.footer button:disabled{
    background-color: grey !important;
}

footer p{
    font-weight: 700;
    color: #000;
}

.footer p span a{
    font-weight: bold;
    color:#9900ff;
    letter-spacing: 1px;
    font-size: 1rem;
    text-decoration: none;
}